* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* PokeDex Style */
.homeBtnBox {
  border: 10px red groove;
  background-color: black;
  border-radius: 20px;
  padding: 10px;
  margin: 2px;
}
.homeBtn {
  width: 100%;
  background-color: yellow;
}
.homeBtn:hover {
  background-color: red;
}
.typeBtn {
  width: 100%;
  padding: 5px;
  border: 1px solid black;
  background-color: white;
  color: black;
  display: flex;
  justify-content: space-between;
  font-size: larger;
}
.typeBtn:hover {
  background-color: lightskyblue;
}
.mainBox {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}
.typeBox {
width: 20%;
margin: 0 auto;
}
.pokemonBox {
  width: 70%;
  margin: 0 auto;
  padding: 3px;
  border: 5px solid black;
  border-radius: 10px;
}
.tableContainer {
  border: white 3px solid;
  background-color: darkgreen;
  border-radius: 10px;
  padding: 3px;
  margin: 0 auto;
  width: 20%;
  float: left;
}
.picture {
  width: 100%;
  height: 150px;
}
.detailBox {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  border: 10px red ridge;
  width: 90%;
}
.detailName {
  width: 100%;
  font-size: 40px;
  padding: 3px;
  background-color: yellow;
  color: black;
  border: black solid 10px;
}
.detailImg {
  width: 100%;
  padding: 10px;
  border: black solid 10px;
}
.detailPic {
  width: 80%;
}
.footer {
  width: 100%;
  background-color: black;
}
.detailType {
  background-color: red;
  border: black 2px solid;
  padding: 1px 10px 1px 10px;
  border-radius: 10px;
  width: fit-content;
  margin: 0 auto;
  margin-top: 10px;
}
.detail {
  color: yellow;
  margin-bottom: 10px;
  padding: 10px;
  font-size: large;
}
.pokeball {
  width: 20px;
  height: 20px;
}

@media only screen and (min-width: 991px) {
  .detailBox {
    margin: 0 auto;
    margin-top: 20px;
    width: 40%;
  }
}

/*  */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
